<template>
  <div>
    <el-header style="text-align: right; font-size: 12px;background-color: #2b72cf;">
      <span style="float:left;font-size:28px;color:#fff;">车辆管理系统</span>
      <!-- <el-dropdown>
        <i class="el-icon-setting" style="margin-right: 15px"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item>查看</el-dropdown-item>
          <el-dropdown-item>新增</el-dropdown-item>
          <el-dropdown-item>删除</el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown> -->
      <span style="color:#fff;">王小虎</span>
    </el-header>
<el-container style="height: 90vh; border: 1px solid #eee">
  <el-aside width="30vh" style="background-color: rgb(238, 241, 246)">
    <el-menu :default-active="sele" @select="handleSelect">
      <el-menu-item index="1">
        <i class="el-icon-menu"></i>
        <span slot="title">统计报表</span>
      </el-menu-item>
      <el-menu-item index="2">
        <i class="el-icon-setting"></i>
        <span slot="title">车辆信息</span>
      </el-menu-item>
      <el-menu-item index="3">
        <i class="el-icon-setting"></i>
        <span slot="title">加油记录</span>
      </el-menu-item>
      <el-menu-item index="4">
        <i class="el-icon-setting"></i>
        <span slot="title">违章记录</span>
      </el-menu-item>
      <el-menu-item index="5">
        <i class="el-icon-setting"></i>
        <span slot="title">保险信息</span>
      </el-menu-item>
      <el-menu-item index="6">
        <i class="el-icon-setting"></i>
        <span slot="title">年检信息</span>
      </el-menu-item>
      <el-menu-item index="7">
        <i class="el-icon-setting"></i>
        <span slot="title">维修记录</span>
      </el-menu-item>
      <el-menu-item index="8">
        <i class="el-icon-setting"></i>
        <span slot="title">出车记录</span>
      </el-menu-item>
    </el-menu>
  </el-aside>
  
  <el-container>

    
    <el-main>
      <component :is="componentTag"></component>
    </el-main>
  </el-container>
</el-container>
</div>

</template>

<style>
  .el-header {
    background-color: #2b72cf;
    color: #333;
    line-height: 60px;
  }
  
  .el-aside {
    color: #333;
  }
</style>

<script>
import tjbb from "./tjbb.vue";
import clxx from '@/components/clxx';
import jyjl from '@/components/jyjl';
import wzjl from './wzjl.vue';
import bxxx from '@/components/bxxx'
import njxx from '@/components/njxx'
import wxjl from './wxjl.vue';
import ccjl from './ccjl.vue';
  export default {
    data() {
      const item = {
        date: '2016-05-02',
        name: '王小虎',
        address: '上海市普陀区金沙江路 1518 弄'
      };
      return {
        tableData: Array(20).fill(item),
        componentTag:"",
        sele:"1"

      }
    },
    mounted(){
      this.componentTag=tjbb;
    },
    components:{
    clxx,tjbb,jyjl,wzjl,bxxx,njxx,wxjl,ccjl
    },
    methods:{
      handleSelect(data){
        switch(data){
          case "1":
          this.componentTag=tjbb;
            break;
            case "2":
            this.componentTag=clxx;
              break;
              case "3":
            this.componentTag=jyjl;
              break;
              case "4":
            this.componentTag=wzjl;
              break;
              case "5":
                this.componentTag=bxxx;
                break;
              case "6":
                this.componentTag=njxx;
                break;
                case "7":
                this.componentTag=wxjl;
                break;
                case "8":
                this.componentTag=ccjl;
                break;
        }
      }
    }
  };
</script>


